@import "index"

.first
  color: #fff
  font-family: 'Proxima Nova', sans-serif
  @extend .column
  justify-content: space-around
  min-height: 100vh
  min-width: 100vw
  overflow: hidden
  position: relative

  &:before
    content: ""
    position: absolute
    left: 0
    right: 0
    top: 0
    bottom: 0
    background-image: url('../images/bg.jpg')
    background-size: cover
    background-position: top center
    z-index: -1 /* Ensures it's behind the rest of the content */

  .navbar
    padding-top: Rem(32)
    .container
      @extend .row
      justify-content: space-between
      align-items: center


    

    &-mobile
      position: fixed
      top: 0 
      right: 0
      background: #1c1c1c
      width: 70%
      height: 100%
      z-index: 5
      transform: translateX(100%)
      transition: all .8s ease

      &-buttons
        margin-top: Rem(25)
        @extend .row
        justify-content: space-between
        +mobile
          flex-flow: column nowrap

        .button
          padding: Rem(12) Rem(25)
          margin-top: Rem(15)
          border-width: 2px
          flex: 1 0 auto
          max-width: 49%
          +mobile
            max-width: 100%

          &:hover
            background: #fff
            color: #000

      &-close
        width: 80%
        margin: 0 auto
        margin-top: Rem(50)
        img
          height: 25px
          cursor: pointer

      &-list
        width: 80%
        margin: 0 auto
        margin-top: Rem(50)
        opacity: 0
        transition: opacity 0.5s ease
        transition-delay: 0.6s



      &-item
        margin-top: Rem(40)
        position: relative
        &:hover
          &:after
            width: 80%
            background: #eee

        &:after
          content: ''
          position: absolute
          display: block
          bottom: -5px
          left: 0
          height: 2px
          width: 100%
          background: #3a3a3a
          transition: all 0.3s ease


        a
          color: #fff
          font-size: Rem(30)
          outline: none
          display: block
          &:focus
            outline: none


    &-mobile-active
      transform: translateX(0%)
      height: 120%

      .navbar-mobile-list
        opacity: 1
        

    &-logo
      flex: 1 0 auto
      max-width: Rem(324)
      @extend .row
      justify-content: flex-start
      +tablet
        max-width: 50%

    &-list
      @extend .row
      flex: 1 0 auto
      justify-content: space-around
      align-items: center

      +tablet
        display: none

      +mobile
        display: none

      &-item
        padding: 0 1.5rem
        +normal
          padding: 0 0.5rem

        a
          font-size: Rem(16)
          color: #fff
          opacity: 0.9
          +normal
            font-size: 0.9rem

          &:hover
            opacity: 1

    &-button
      flex: 1 0 auto
      max-width: 50%
      @extend .column
      align-items: flex-end
      cursor: pointer

      span
        height: 2px 
        display: block 
        background-color: #fff
        width: 25px
        margin-top: 6px
        &:first-child
          margin-top: 0


      +hd
        display: none
      +normal
        display: none


    &-buttons
      @extend .row

      .button
        padding: Rem(12) Rem(25)
        margin: 2px
        margin-top: 0
        margin-bottom: 0
        +normal
          padding: 0.45rem 1.0625rem
          border: 2px #fff solid
          font-size: Rem(12)
        &:hover
          background: #fff
          color: #000



  .headers
    @extend .column 
    justify-content: center
    align-items: center
    flex: 1 0 auto
    .container
      padding-top: Rem(50)
      @extend .column
      align-items: center

    .header
      margin-top: Rem(57)
      font-size: Rem(35)
      max-width: Rem(690)
      line-height: Rem(40)
      +tablet
        font-size: 3.5vw
      +mobile
        font-size: 1.5875rem

  
  .buttons 
    padding-bottom: Rem(150)
    +mobile
      padding-bottom: Rem(100)
    @extend .column
    justify-content: center
    .container
      @extend .row 
      justify-content: center
      align-items: center
      +mobile
        flex-flow: column nowrap

      span
        padding: 0 Rem(22)
        +mobile
          padding: Rem(20) Rem(32)

      .button
        padding: Rem(12) Rem(39)
        margin: 3px
        text-transform: uppercase
        letter-spacing: 1px
        background: #da2d2d
        margin-top: 0
        margin-bottom: 0

        +mobile
          font-size: 1.2375rem
          padding: Rem(12) Rem(23)
